.Preview {
  width: 300px;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  position: relative;
  overflow: hidden; }
  .Preview > .Wrapper {
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    height: 100%; }
    .Preview > .Wrapper > .Image {
      -webkit-animation: background-zoom-out 0.4s ease-out both;
      animation: background-zoom-out 0.4s ease-out both;
      position: absolute;
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-size: 100% 100%;
      overflow: hidden; }
      .Preview > .Wrapper > .Image:before {
        content: " ";
        position: absolute;
        width: 100%;
        height: 100%; }
    .Preview > .Wrapper > .Content {
      -webkit-animation: slide-out-top 0.4s cubic-bezier(0.94, 0.45, 0.46, 0.25) both;
      animation: slide-out-top 0.4s cubic-bezier(0.94, 0.45, 0.46, 0.25) both;
      padding: 10% 5%;
      position: absolute;
      display: flex;
      flex-direction: column;
      justify-content: center; }
    .Preview > .Wrapper:hover > .Image {
      -webkit-animation: background-zoom-in 0.4s ease-out both;
      animation: background-zoom-in 0.4s ease-out both; }
      .Preview > .Wrapper:hover > .Image:before {
        transition: all 0.4s ease-in-out 0s;
        background-color: rgba(43, 42, 42, 0.8); }
    .Preview > .Wrapper:hover > .Content {
      -webkit-animation: slide-in-top 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
      animation: slide-in-top 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
      display: block; }

